"use client";

import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Checkbox } from "@/components/ui/checkbox";
import { Slider } from "@/components/ui/slider";
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from "@/components/ui/accordion";

interface Property {
  id: string;
  title: string;
  price: number;
  address: string;
  area: string;
  bedrooms: number;
  bathrooms: number;
  size: number;
  propertyType: string;
  furnished: boolean;
  petFriendly: boolean;
  amenities: string[];
  ownerName: string;
  ownerPhone: string;
  availableFrom: string;
  isNew?: boolean;
  isTemporal?: boolean;
}

interface Filters {
  priceRange: [number, number];
  propertyTypes: string[];
  rooms: string;
  sizeMin: string;
  sizeMax: string;
  features: string[];
  rentalType: string[];
  location: string;
}

export default function PropiedadesPage() {
  const [properties, setProperties] = useState<Property[]>([]);
  const [filteredProperties, setFilteredProperties] = useState<Property[]>([]);
  const [filters, setFilters] = useState<Filters>({
    priceRange: [0, 5000],
    propertyTypes: [],
    rooms: '',
    sizeMin: '',
    sizeMax: '',
    features: [],
    rentalType: [],
    location: ''
  });
  const [sortBy, setSortBy] = useState('relevancia');
  const [viewMode, setViewMode] = useState<'grid' | 'list'>('grid');
  const [showMap, setShowMap] = useState(true);
  const [searchQuery, setSearchQuery] = useState('');
  const [isLoading, setIsLoading] = useState(true);

  const [error, setError] = useState<string | null>(null);

  // Property type mapping for display
  const getPropertyTypeDisplay = (type: string): string => {
    const typeMap: { [key: string]: string } = {
      'apartment': 'Apartamento',
      'house': 'Casa',
      'studio': 'Estudio',
      'room': 'Habitaci√≥n'
    };
    return typeMap[type] || type;
  };

  useEffect(() => {
    async function fetchProperties() {
      try {
        setIsLoading(true);
        setError(null);
        
        const res = await fetch('/api/property/list?limit=100');
        if (!res.ok) {
          throw new Error("Error en la llamada a la API");
        }
        
        const data = await res.json();
        
        if (data.success && data.properties) {
          // Map API response to local state
          const mapped = data.properties.map((prop: any) => ({
            id: prop.id,
            title: prop.title,
            price: prop.calendar?.pricing?.winter || 0,
            address: prop.address,
            area: prop.area,
            bedrooms: prop.bedrooms,
            bathrooms: prop.bathrooms,
            size: prop.size,
            propertyType: prop.propertyType,
            furnished: prop.furnished,
            petFriendly: prop.petFriendly,
            amenities: prop.amenities || [],
            ownerName: prop.ownerName,
            ownerPhone: prop.ownerPhone,
            availableFrom: prop.availableFrom,
            isNew: Math.random() > 0.7, // Random for demo
            isTemporal: Math.random() > 0.5 // Random for demo
          }));
          
          setProperties(mapped);
          setFilteredProperties(mapped);
        } else {
          throw new Error("Formato de respuesta inv√°lido");
        }
      } catch (err) {
        console.error('Error fetching properties:', err);
        setError("Error al cargar propiedades. Por favor, intente nuevamente.");
      } finally {
        setIsLoading(false);
      }
    }
    
    fetchProperties();
  }, []);

  useEffect(() => {
    applyFilters();
  }, [filters, properties, sortBy]);

  const applyFilters = () => {
    let filtered = [...properties];

    // Price filter
    filtered = filtered.filter(p => 
      p.price >= filters.priceRange[0] && p.price <= filters.priceRange[1]
    );

    // Property type filter
    if (filters.propertyTypes.length > 0) {
      filtered = filtered.filter(p => filters.propertyTypes.includes(p.propertyType));
    }

    // Rooms filter
    if (filters.rooms && filters.rooms !== 'all') {
      if (filters.rooms === '5+') {
        filtered = filtered.filter(p => p.bedrooms >= 5);
      } else {
        filtered = filtered.filter(p => p.bedrooms === parseInt(filters.rooms));
      }
    }

    // Size filter
    if (filters.sizeMin) {
      filtered = filtered.filter(p => p.size >= parseInt(filters.sizeMin));
    }
    if (filters.sizeMax) {
      filtered = filtered.filter(p => p.size <= parseInt(filters.sizeMax));
    }

    // Features filter
    if (filters.features.length > 0) {
      filtered = filtered.filter(p => 
        filters.features.some(feature => p.amenities.includes(feature))
      );
    }

    // Rental type filter
    if (filters.rentalType.length > 0) {
      filtered = filtered.filter(p => {
        if (filters.rentalType.includes('temporal') && p.isTemporal) return true;
        if (filters.rentalType.includes('larga-temporada') && !p.isTemporal) return true;
        return false;
      });
    }

    // Location filter
    if (filters.location) {
      filtered = filtered.filter(p => 
        p.address.toLowerCase().includes(filters.location.toLowerCase()) ||
        p.area.toLowerCase().includes(filters.location.toLowerCase())
      );
    }

    // Sort
    switch (sortBy) {
      case 'precio-asc':
        filtered.sort((a, b) => a.price - b.price);
        break;
      case 'precio-desc':
        filtered.sort((a, b) => b.price - a.price);
        break;
      case 'mas-recientes':
        filtered.sort((a, b) => (b.isNew ? 1 : 0) - (a.isNew ? 1 : 0));
        break;
      default:
        // Relevancia - keep original order
        break;
    }

    setFilteredProperties(filtered);
  };

  const handleFilterChange = (key: keyof Filters, value: any) => {
    setFilters(prev => ({ ...prev, [key]: value }));
  };

  const toggleFeature = (feature: string) => {
    setFilters(prev => ({
      ...prev,
      features: prev.features.includes(feature)
        ? prev.features.filter(f => f !== feature)
        : [...prev.features, feature]
    }));
  };

  const togglePropertyType = (type: string) => {
    setFilters(prev => ({
      ...prev,
      propertyTypes: prev.propertyTypes.includes(type)
        ? prev.propertyTypes.filter(t => t !== type)
        : [...prev.propertyTypes, type]
    }));
  };

  const toggleRentalType = (type: string) => {
    setFilters(prev => ({
      ...prev,
      rentalType: prev.rentalType.includes(type)
        ? prev.rentalType.filter(t => t !== type)
        : [...prev.rentalType, type]
    }));
  };

  return (
    <div className="min-h-screen bg-gray-50 pt-16">
      {/* Top Menu Bar */}
      <div className="bg-white border-b border-gray-200 sticky top-16 z-40">
        <div className="max-w-7xl mx-auto px-4 py-3">
          <div className="flex items-center justify-between">
            <nav className="flex space-x-6">
              <Button variant="ghost" className="text-primary font-semibold">Alquilar</Button>
              <Button variant="ghost" className="text-gray-600 hover:text-primary">Comprar</Button>
              <Button variant="ghost" className="text-gray-600 hover:text-primary">Alquiler Vacacional</Button>
              <Button variant="ghost" className="text-gray-600 hover:text-primary">Proyectos Nuevos</Button>
              <Button variant="ghost" className="text-gray-600 hover:text-primary">Locales</Button>
            </nav>
            <div className="flex items-center space-x-4">
              <Button className="bg-primary hover:bg-primary/90 text-secondary">
                Publicar anuncio
              </Button>
              <Button variant="outline">Iniciar sesi√≥n</Button>
              <Button variant="ghost" size="sm">üåê ES</Button>
              <Button variant="ghost" size="sm">‚ùì</Button>
              <Button variant="ghost" size="sm">üîî</Button>
            </div>
          </div>
        </div>
      </div>

      {/* Search Bar */}
      <div className="bg-white border-b border-gray-200 py-4">
        <div className="max-w-7xl mx-auto px-4">
          <div className="flex items-center space-x-4">
            <div className="flex-1 relative">
              <Input
                placeholder="Buscar por ciudad, barrio o estaci√≥n..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="pl-4 pr-12 py-3 text-lg"
              />
              <Button 
                size="sm" 
                className="absolute right-2 top-1/2 transform -translate-y-1/2 bg-primary hover:bg-primary/90"
              >
                üîç
              </Button>
            </div>
            <Button 
              variant="outline" 
              onClick={() => setShowMap(!showMap)}
              className={showMap ? 'bg-primary text-secondary' : ''}
            >
              {showMap ? 'Ocultar mapa' : 'Ver mapa'}
            </Button>
          </div>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-4 py-6">
        <div className="flex gap-6">
          {/* Left Sidebar - Filters */}
          <div className="w-80 flex-shrink-0">
            <Card className="sticky top-32">
              <CardHeader>
                <CardTitle className="text-lg">Filtros</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <Accordion type="multiple" defaultValue={["precio", "tipo", "habitaciones"]}>
                  {/* Price Range */}
                  <AccordionItem value="precio">
                    <AccordionTrigger>Rango de Precio</AccordionTrigger>
                    <AccordionContent className="space-y-4">
                      <div className="px-2">
                        <Slider
                          value={filters.priceRange}
                          onValueChange={(value) => handleFilterChange('priceRange', value)}
                          max={5000}
                          min={0}
                          step={50}
                          className="w-full"
                        />
                        <div className="flex justify-between text-sm text-gray-600 mt-2">
                          <span>‚Ç¨{filters.priceRange[0]}</span>
                          <span>‚Ç¨{filters.priceRange[1]}</span>
                        </div>
                      </div>
                    </AccordionContent>
                  </AccordionItem>

                  {/* Property Type */}
                  <AccordionItem value="tipo">
                    <AccordionTrigger>Tipo de Propiedad</AccordionTrigger>
                    <AccordionContent className="space-y-3">
                      {['apartment', 'house', 'studio', 'room'].map(type => (
                        <div key={type} className="flex items-center space-x-2">
                          <Checkbox
                            id={type}
                            checked={filters.propertyTypes.includes(type)}
                            onCheckedChange={() => togglePropertyType(type)}
                          />
                          <Label htmlFor={type} className="text-sm">{getPropertyTypeDisplay(type)}</Label>
                        </div>
                      ))}
                    </AccordionContent>
                  </AccordionItem>

                  {/* Rooms */}
                  <AccordionItem value="habitaciones">
                    <AccordionTrigger>Habitaciones</AccordionTrigger>
                    <AccordionContent>
                      <Select value={filters.rooms} onValueChange={(value) => handleFilterChange('rooms', value)}>
                        <SelectTrigger>
                          <SelectValue placeholder="Cualquiera" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="all">Cualquiera</SelectItem>
                          <SelectItem value="1">1 habitaci√≥n</SelectItem>
                          <SelectItem value="2">2 habitaciones</SelectItem>
                          <SelectItem value="3">3 habitaciones</SelectItem>
                          <SelectItem value="4">4 habitaciones</SelectItem>
                          <SelectItem value="5+">5 o m√°s</SelectItem>
                        </SelectContent>
                      </Select>
                    </AccordionContent>
                  </AccordionItem>

                  {/* Size */}
                  <AccordionItem value="metros">
                    <AccordionTrigger>Metros Cuadrados</AccordionTrigger>
                    <AccordionContent className="space-y-3">
                      <div className="grid grid-cols-2 gap-2">
                        <div>
                          <Label className="text-xs">M√≠nimo</Label>
                          <Input
                            placeholder="Min m¬≤"
                            value={filters.sizeMin}
                            onChange={(e) => handleFilterChange('sizeMin', e.target.value)}
                            type="number"
                          />
                        </div>
                        <div>
                          <Label className="text-xs">M√°ximo</Label>
                          <Input
                            placeholder="Max m¬≤"
                            value={filters.sizeMax}
                            onChange={(e) => handleFilterChange('sizeMax', e.target.value)}
                            type="number"
                          />
                        </div>
                      </div>
                    </AccordionContent>
                  </AccordionItem>

                  {/* Features */}
                  <AccordionItem value="caracteristicas">
                    <AccordionTrigger>Caracter√≠sticas</AccordionTrigger>
                    <AccordionContent className="space-y-3">
                      {['Ascensor', 'Piscina', 'Jard√≠n', 'Amueblado', 'Mascotas permitidas', 'Garaje', 'Terraza', 'Aire acondicionado'].map(feature => (
                        <div key={feature} className="flex items-center space-x-2">
                          <Checkbox
                            id={feature}
                            checked={filters.features.includes(feature)}
                            onCheckedChange={() => toggleFeature(feature)}
                          />
                          <Label htmlFor={feature} className="text-sm">{feature}</Label>
                        </div>
                      ))}
                    </AccordionContent>
                  </AccordionItem>

                  {/* Rental Type */}
                  <AccordionItem value="alquiler">
                    <AccordionTrigger>Tipo de Alquiler</AccordionTrigger>
                    <AccordionContent className="space-y-3">
                      <div className="flex items-center space-x-2">
                        <Checkbox
                          id="temporal"
                          checked={filters.rentalType.includes('temporal')}
                          onCheckedChange={() => toggleRentalType('temporal')}
                        />
                        <Label htmlFor="temporal" className="text-sm">Temporal</Label>
                      </div>
                      <div className="flex items-center space-x-2">
                        <Checkbox
                          id="larga-temporada"
                          checked={filters.rentalType.includes('larga-temporada')}
                          onCheckedChange={() => toggleRentalType('larga-temporada')}
                        />
                        <Label htmlFor="larga-temporada" className="text-sm">Larga temporada</Label>
                      </div>
                    </AccordionContent>
                  </AccordionItem>
                </Accordion>

                <Button 
                  variant="outline" 
                  className="w-full"
                  onClick={() => setFilters({
                    priceRange: [0, 5000],
                    propertyTypes: [],
                    rooms: '',
                    sizeMin: '',
                    sizeMax: '',
                    features: [],
                    rentalType: [],
                    location: ''
                  })}
                >
                  Limpiar filtros
                </Button>
              </CardContent>
            </Card>
          </div>

          {/* Main Content */}
          <div className="flex-1">
            {/* Results Bar */}
            <div className="bg-white rounded-lg border border-gray-200 p-4 mb-6">
              <div className="flex items-center justify-between">
                <div className="flex items-center space-x-4">
                  <span className="text-lg font-semibold">
                    {filteredProperties.length} propiedades encontradas
                  </span>
                  <Button variant="ghost" size="sm" className="text-primary">
                    ‚ù§Ô∏è Guardar b√∫squeda
                  </Button>
                  <Button variant="ghost" size="sm" className="text-primary">
                    üì§ Compartir
                  </Button>
                </div>
                <div className="flex items-center space-x-4">
                  <Select value={sortBy} onValueChange={setSortBy}>
                    <SelectTrigger className="w-48">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="relevancia">Relevancia</SelectItem>
                      <SelectItem value="precio-asc">Precio (menor a mayor)</SelectItem>
                      <SelectItem value="precio-desc">Precio (mayor a menor)</SelectItem>
                      <SelectItem value="mas-recientes">M√°s recientes</SelectItem>
                    </SelectContent>
                  </Select>
                  <div className="flex border rounded-lg">
                    <Button
                      variant={viewMode === 'grid' ? 'default' : 'ghost'}
                      size="sm"
                      onClick={() => setViewMode('grid')}
                      className="rounded-r-none"
                    >
                      ‚äû
                    </Button>
                    <Button
                      variant={viewMode === 'list' ? 'default' : 'ghost'}
                      size="sm"
                      onClick={() => setViewMode('list')}
                      className="rounded-l-none"
                    >
                      ‚ò∞
                    </Button>
                  </div>
                </div>
              </div>
            </div>

            <div className="flex gap-6">
              {/* Properties List */}
              <div className={showMap ? 'flex-1' : 'w-full'}>
                <section id="resultados-busqueda" className="bg-white rounded-lg border border-gray-200 p-6">
                  {/* Cabecera de Resultados */}
                  <div className="mb-6">
                    <h2 className="text-2xl font-bold text-gray-800 mb-2">Sigue con tu √∫ltima b√∫squeda</h2>
                    <p className="text-gray-600 text-lg">Inmuebles en Madrid</p>
                  </div>

                  {isLoading ? (
                    <div className="text-center py-12">
                      <div className="animate-spin w-12 h-12 border-4 border-primary border-t-transparent rounded-full mx-auto mb-4"></div>
                      <p className="text-gray-600">Cargando propiedades...</p>
                    </div>
                  ) : error ? (
                    <div className="text-center py-12">
                      <p className="text-red-600 mb-4">{error}</p>
                      <Button 
                        onClick={() => window.location.reload()}
                        className="bg-primary hover:bg-primary/90"
                      >
                        Reintentar
                      </Button>
                    </div>
                  ) : (
                    <>
                      {/* Listado de Propiedades */}
                      <div className={viewMode === 'grid' 
                        ? "grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6" 
                        : "flex flex-col space-y-6"
                      }>
                        {filteredProperties.map((property) => (
                          <article 
                            key={property.id} 
                            className="propiedad-card hover:shadow-lg transition-shadow duration-200 p-4 rounded-lg border border-gray-100"
                            role="article"
                            aria-label={`Propiedad: ${property.address}, ${property.area}`}
                          >
                            {/* Precio */}
                            <h3 className="text-3xl font-bold text-gray-800 mb-2">
                              {property.price} ‚Ç¨
                            </h3>
                            
                            {/* Tipo de propiedad */}
                            <div className="mb-3">
                              <span className="inline-block bg-blue-50 text-blue-800 px-3 py-1 rounded text-sm font-medium">
                                {getPropertyTypeDisplay(property.propertyType)}
                              </span>
                              {property.isNew && (
                                <span className="inline-block bg-green-50 text-green-800 px-3 py-1 rounded text-sm font-medium ml-2">
                                  Nuevo hoy
                                </span>
                              )}
                              {property.isTemporal && (
                                <span className="inline-block bg-orange-50 text-orange-800 px-3 py-1 rounded text-sm font-medium ml-2">
                                  Alquiler temporal
                                </span>
                              )}
                            </div>
                            
                            {/* Direcci√≥n */}
                            <div className="mb-3">
                              <p className="text-gray-800 font-medium text-base">
                                {property.address}
                              </p>
                              <p className="text-gray-600 text-sm">
                                {property.area}
                              </p>
                            </div>
                            
                            {/* Caracter√≠sticas clave */}
                            <p className="text-gray-700 mb-4">
                              {property.bedrooms} habs. ¬∑ {property.bathrooms} ba√±os ¬∑ {property.size} m¬≤
                            </p>
                            
                            {/* Caracter√≠sticas adicionales */}
                            {property.amenities.length > 0 && (
                              <div className="mb-4">
                                <div className="flex flex-wrap gap-2">
                                  {property.amenities.slice(0, 4).map((amenity, idx) => (
                                    <span key={idx} className="text-xs bg-gray-100 text-gray-700 px-2 py-1 rounded">
                                      {amenity}
                                    </span>
                                  ))}
                                  {property.amenities.length > 4 && (
                                    <span className="text-xs bg-gray-100 text-gray-700 px-2 py-1 rounded">
                                      +{property.amenities.length - 4} m√°s
                                    </span>
                                  )}
                                </div>
                              </div>
                            )}
                            
                            {/* Separador visual */}
                            <hr className="border-gray-200 my-4" />
                            
                            {/* Secci√≥n de Contacto */}
                            <div className="contacto flex items-center justify-between">
                              <div className="flex space-x-3">
                                <Button 
                                  className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded transition-colors"
                                  aria-label="Contactar sobre esta propiedad"
                                >
                                  Contactar
                                </Button>
                                <Button 
                                  variant="outline"
                                  className="border-blue-600 text-blue-600 hover:bg-blue-50 px-4 py-2 rounded transition-colors"
                                  aria-label="Llamar sobre esta propiedad"
                                  onClick={() => window.location.href = `tel:${property.ownerPhone}`}
                                >
                                  Llamar
                                </Button>
                              </div>
                              <div className="flex space-x-2">
                                <Button 
                                  variant="ghost" 
                                  size="sm"
                                  aria-label="A√±adir a favoritos"
                                  className="text-gray-500 hover:text-red-500 transition-colors"
                                >
                                  ‚ô°
                                </Button>
                                <Button 
                                  variant="ghost" 
                                  size="sm"
                                  aria-label="Compartir propiedad"
                                  className="text-gray-500 hover:text-blue-500 transition-colors"
                                >
                                  ‚Üó
                                </Button>
                              </div>
                            </div>
                          </article>
                        ))}
                      </div>

                      {/* Elemento Final */}
                      <div className="text-center mt-8">
                        <Button 
                          id="ver-todos"
                          variant="outline"
                          className="border-blue-600 text-blue-600 hover:bg-blue-50 px-8 py-3 rounded-lg font-medium transition-colors"
                          aria-label="Mostrar todos los inmuebles disponibles"
                        >
                          Mostrar todos los inmuebles
                        </Button>
                      </div>
                    </>
                  )}
                </section>
              </div>

              {/* Map */}
              {showMap && (
                <div className="w-96 flex-shrink-0">
                  <Card className="sticky top-32 h-96">
                    <CardContent className="p-4 h-full">
                      <div className="bg-gray-100 rounded-lg h-full flex items-center justify-center">
                        <div className="text-center">
                          <div className="text-4xl mb-2">üó∫Ô∏è</div>
                          <p className="text-gray-600">Mapa Interactivo</p>
                          <p className="text-sm text-gray-500 mt-2">
                            {filteredProperties.length} propiedades en el mapa
                          </p>
                          <Button variant="outline" size="sm" className="mt-3">
                            Dibujar √°rea
                          </Button>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
